@import "variables.less";
@import "mixins.less";

// Custom Admin is here
.href {
	cursor: pointer;
}
.login_header {
	// background: @brand-primary;
	// padding-top: 0px;
	color:	@brand-primary;
	.beepond_logo {
		color:	@brand-primary;
		// font-weight:300;
		font-size: @font-size-h4;
		padding:0.5em;
		small {
			font-size: @font-size-h5;
		}
	}
}
.graybox {
	margin-top:2em;
}
.pagination {
	margin: 0px;
}

input[type=text].form-control.error, select.form-control.error {
	border:1px solid @state-danger-text;
	background: @state-danger-bg;
}

.panel-title {
	.switch {
	    float:left;
	    margin-right:5px;
	    position: relative;
	    
	    input[type="checkbox"] {
	    	display: none;
	    }
	    label {
	    	color: @panel-default-text;
	    	// transition: all 0.3s ease-out;
    		// z-index: -1;
    		margin-bottom:0;
    		margin-top:-@line-height-computed;
    		cursor: pointer;
	    }
	    label > i.inverted.icon {
	    	background:#fff;
	    	color: lighten(@panel-default-text, 40%);
	    	border: 1px solid @panel-default-border;
	    	&:hover, &:focus {
	    		background:#fff;
	    		color: @panel-default-text;
	    		border: 1px solid @panel-default-border;
	    	}
	    }
	    // input[type="checkbox"]:checked + label {
	    // 	color: @alert-success-text;
	    // }
	    input[type="checkbox"]:checked + label > i.inverted.icon {
	    	background: @brand-primary;
	    	color: @panel-primary-text;
	    	border: 1px solid @panel-default-border;
	    }
  	}
}
.panel-body {
	label {
		font-weight: normal;
	}
}

//Modal Body Heading
.modal-body {
	h1, h2, h3, .h1, .h2, .h3 {
		margin-top: 0;
	}
}

/* Icons */
.originfb {
	color: #FFFFFF;
	background-color:#3B5998;
	border: none;
	-webkit-box-shadow: none;
	box-shadow: none;
}
/* Icon Feeds Toggle */
// i.icon.checkmark:before { content: "\f00c"; }
// i.icon.ban.circle:before { content: "\f05e"; }
.feedswitch {
	input[type="checkbox"] {
    	display: none;
    }
    label {
		cursor: pointer;
	    }
	label > i.icon.feedstatus:before {
		content: "\f05e"; //ban circle
    	color: #D95C5C !important; // red
    	font-size: 1.6em;
    }
	input[type="checkbox"]:checked + label > i.icon.feedstatus:before {
    	content: "\f00c"; //checked
    	color: #A1CF64 !important; // green
    }
}

.feedtypegroup {
	input[type="checkbox"] {
    	display: none;
    }
    label {
    	float: left;
		cursor: pointer;
		border-radius: @border-radius-base;
	}
	label.istext {
		border-top-right-radius: 0;
		border-bottom-right-radius: 0;
	}
	label.isimage {
		border-radius: 0;
	}
	label.isvideo {
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
	}
	input[type="checkbox"]:checked + label {
  		.button-variant(@btn-success-color; @btn-success-bg; @btn-success-border);
    }
}

.form-group {
	margin-left: 0px !important; // override
	margin-right: 0px !important; // override
}

// Responsive Override
@media (min-width: @screen-sm-min) {
	// Fix Dropdown issue on iPad.
	.nav {
		position: relative;
		z-index: 2;		
	}
	h1, h2, h3, .h1, .h2, .h3 {
		position: relative;
		z-index: 1;
	}
}
@media (min-width: @screen-md-min) {
	
}
@media (min-width: @screen-lg-min) {
	// Override Modal Shifting Issue pure CSS
	html {
	  -ms-overflow-style: scrollbar;
	  overflow:hidden;
	  height:100%;
	  body {
		  overflow:auto;
		  height:100%;
		}
	}
	/* unset bs3 setting */
	.modal-open {
		overflow:auto; 
	}
}
